:root {
  --prim-line-grad-start: hsl(236, 72%, 79%);
  --prim-line-grad-end: hsl(237, 63%, 64%);
  --neu-very-light-gray-blue: hsl(240, 78%, 98%);
  --neu-light-gray-blue: hsl(234, 14%, 74%);
  --neu-gray-blue: hsl(233, 13%, 49%);
  --neu-dark-gray-blue: hsl(232, 13%, 33%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --background-top: url("../images/bg-top.svg");
  --background-bottom: url("../images/bg-bottom.svg");
}

body {
  font-size: 15px;
  color: var(--neu-gray-blue);
  background-color: var(--neu-very-light-gray-blue);
  background-image: var(--background-top), var(--background-bottom);
  background-repeat: no-repeat, no-repeat;
  background-size: 12.5rem, 12.5rem;
  background-position: 175% -3%, -50% 100%;
  text-align: center;
}

h1,
h2,
h3 {
  margin-top: 0;
}

h3 {
  font-size: calc(0.85rem + 0.1vw);
}

/* screen reader only class */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  color: transparent;
}

:root {
  --font-montserrat: "Montserrat", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-montserrat);
  min-width: 18.75rem;
  max-width: 100%;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.header {
  padding: 1.875rem;
  margin: 0 1.25rem;
}

.main {
  margin: 0 1.25rem;
}

.pricing {
  width: 100%;
}
.pricing__card {
  width: 100%;
  max-width: 21.875rem;
  background: var(--white);
  box-shadow: 0 0 2.5rem 0.3125rem var(--neu-light-gray-blue);
  margin-bottom: 1.5rem;
  border-radius: 0.625rem;
  padding: 1.5rem;
}
.pricing__title {
  margin: 0.8rem 0;
}
.pricing__cost {
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--neu-dark-gray-blue);
}
.pricing__cost--monthly {
  display: none;
}
.pricing__digits {
  font-size: 3rem;
  margin: 0 0.5rem;
}
.pricing__features {
  padding: 0;
  margin-bottom: 1.5rem;
}
.pricing__feature {
  padding: 0.8rem;
  border-top: 0.0625rem solid var(--neu-light-gray-blue);
}
.pricing__feature:last-of-type {
  border-bottom: 0.0625rem solid var(--neu-light-gray-blue);
}
.pricing__cta {
  display: block;
  background: linear-gradient(to right, var(--prim-line-grad-start), var(--prim-line-grad-end));
  color: var(--white);
  text-transform: uppercase;
  padding: 0.75rem;
  letter-spacing: 0.0625rem;
  font-size: calc(0.85rem + 0.1vw);
  border-radius: 0.375rem;
  border: 0.125rem solid var(--white);
}
.pricing__cta:hover, .pricing__cta:focus-visible {
  border: 0.125rem solid var(--neu-dark-gray-blue);
  background: none;
  color: var(--neu-dark-gray-blue);
  outline: var(--neu-dark-gray-blue) dotted 0.25rem;
  outline-offset: 0.125rem;
  opacity: 0.7;
}
.pricing__cta:active {
  transition: all 100ms cubic-bezier(0.4, 0, 0.2, 1);
  transform: scale(0.9);
}
.pricing__card--highlight {
  background: linear-gradient(to bottom, var(--prim-line-grad-start), var(--prim-line-grad-end));
  color: var(--white);
}
.pricing__cta--highlight {
  background: var(--white);
  color: var(--neu-gray-blue);
}
.pricing__cta--highlight:hover, .pricing__cta--highlight:focus-visible {
  background: none;
  color: var(--white);
  border: 0.125rem solid var(--white);
  outline: var(--white) dotted 0.25rem;
}
.pricing__cost--highlight {
  color: var(--white);
}

@media only screen and (min-width: 48rem) {
  .pricing {
    filter: drop-shadow(0 0 2.5rem var(--neu-light-gray-blue));
  }
  .pricing__card {
    margin: 0;
    box-shadow: none;
    max-width: 20.3125rem;
  }
  .pricing__card--highlight {
    padding: 3.125rem 1.5rem;
  }
  .pricing__card:first-of-type {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
  .pricing__card:last-of-type {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
}
.toggle {
  /* special states */
  /* checked states */
}
.toggle__title {
  font-size: 0.8125rem;
  color: var(--neu-light-gray-blue);
}
.toggle__title--selected {
  color: var(--neu-gray-blue);
}
.toggle__switch {
  color: var(--neu-light-gray-blue);
  display: inline-flex;
  align-items: center;
  margin: 0.3125rem 0;
}
.toggle__button {
  position: relative;
  cursor: pointer;
  margin: 0 1rem;
}
.toggle__button::before, .toggle__button::after {
  content: "";
  display: block;
  margin: 0 0.1875rem;
  transition: all 100ms cubic-bezier(0.4, 0, 0.2, 1);
}
.toggle__button::before {
  height: 1.5em;
  width: 2.75em;
  border-radius: 0.8em;
  background: linear-gradient(to right, var(--prim-line-grad-start), var(--prim-line-grad-end));
}
.toggle__button::after {
  position: absolute;
  top: 50%;
  left: 7%;
  transform: translate(0, -50%);
  height: 1.1em;
  width: 1.1em;
  border-radius: 50%;
  background: var(--neu-very-light-gray-blue);
  box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.2), 0 0 0.125rem rgba(0, 0, 0, 0.4);
}
.toggle__checkbox:hover + .toggle__button, .toggle__checkbox:focus-visible + .toggle__button {
  outline: var(--neu-dark-gray-blue) dotted 0.25rem;
  outline-offset: 0.125rem;
  opacity: 0.7;
}
.toggle__checkbox:checked + .toggle__button::after {
  transform: translate(calc(2.3em - 100%), -50%);
}